form {
    margin: 10px 0;
}

form > button {
    background: -webkit-gradient(linear, right top, left bottom, from(#00C01C), to(green));
    background: -webkit-linear-gradient(top right, #00C01C, green);
    background: -o-linear-gradient(top right, #00C01C, green);
    background: linear-gradient(to bottom left, #00C01C, green);
    border: none;
    border-radius: 2px;
    color: white;
    font-size: 16px;
    font-family: helvetica;
    font-weight: lighter;
    padding: 5px;
    cursor: pointer;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
}

form > button:hover{
    -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
}

.inputfile {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.inputfile + label {
   background: -webkit-gradient(linear, right top, left bottom, from(rgb(0, 140, 255)), to(blue));
   background: -webkit-linear-gradient(top right, rgb(0, 140, 255), blue);
   background: -o-linear-gradient(top right, rgb(0, 140, 255), blue);
   background: linear-gradient(to bottom left, rgb(0, 140, 255), blue);
    color: white;
    padding: 5px;
    border-radius: 2px;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    cursor: pointer;
    margin-right: 10px;
}

.inputfile:focus + label,
.inputfile + label:hover {
    -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
}




