body {
  margin: 0;
  padding: 0;
  font-family: Helvetica;
  font-weight: lighter;
  background: -webkit-gradient(linear, left top, right bottom, from(#F02400), to(#d2005f));
  background: -webkit-linear-gradient(top left, #F02400, #d2005f);
  background: -o-linear-gradient(top left, #F02400, #d2005f);
  background: linear-gradient(to bottom right, #F02400, #d2005f);
}

h1, h2{
  font-weight: lighter;
}

div{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}


